

export default {
  data () {
    return {
      role: '',
      message: 'Processing...',
      show: false,
      loading: false,
      icon: 'fa-info',

      topAlert: false,
      title: 'Error'
    }
  },
  mounted () {
    this.$nuxt.$on('topAlert', ({ msg, role = 'info', loading = false, timeout = 4000 }) => {
      if (!msg) {
        this.show = false
        return
      }

      const dict = {
        info: 'color-blue-light',
        loading: 'color-blue-light',
        error: 'color-red-light',
        warn: 'color-orange-light',
        success: 'color-green-light'
      }
      this.role = dict[role || 'info']
      const iconDict = {
        info: 'fa-info',
        loading: 'fa-sync',
        error: 'fa-times',
        warn: 'fa-exclamation-triangle',
        success: 'a-check'
      }

      const titleDict = {
        info: 'Information',
        loading: 'Please Wait...',
        error: 'Operation Error',
        warn: 'Warning',
        success: 'Successful'
      }

      this.title = titleDict[role || 'error']

      this.icon = iconDict[role || 'info']
      this.message = msg
      this.loading = loading
      this.topAlert = true
      setTimeout(() => {
        this.topAlert = false
      }, timeout)
    })

    this.$nuxt.$on('alert', ({ msg, role = 'info', loading = false, timeout = 30000 }) => {
      if (!msg) {
        this.show = false
        return
      }

      const dict = {
        info: 'bg-blue-dark',
        loading: 'bg-blue-dark',
        error: 'bg-red-dark',
        warn: 'bg-orange-dark',
        success: 'bg-green-dark'
      }
      this.role = dict[role || 'info']
      const iconDict = {
        info: 'fa-info',
        loading: 'fa-sync',
        error: 'fa-times',
        warn: 'fa-exclamation-triangle',
        success: 'a-check'
      }
      this.icon = iconDict[role || 'info']
      this.message = msg
      this.loading = loading
      this.show = true
      setTimeout(() => {
        this.show = false
      }, timeout)
    })
  },
  methods: {
    open (msg, role = 'info', loading = false, timeout = 15000) {
