
import { UA, WebSocketInterface } from 'jssip'
// import * as JsSIP from 'jssip'
import { parsePhoneNumber /** , AsYouType */ } from 'libphonenumber-js'

// eslint-disable-next-line no-unused-vars
// import { RTCSession } from 'jssip/lib/RTCSession'
import { mapMutations, mapState } from 'vuex'
export default {
  data () {
    return {
      showDialing: false,
      phone: '',
      status: '',
      ongoing: null,
      UA: null,
      ongoingTimer: null,
      lastTimer: null,
      loud: true,
      muted: false,
      audioOutDeviceIndex: 0
    }
  },
  computed: mapState(['creds', 'hasMicAccess', 'connected']),
  mounted () {
    this.$nuxt.$on('dialNumber', this.call)
    this.connect()
    this.$router.beforeEach((_to, _from, next) => {
      if (this.showDialing) {
        next(false)
        return ''
      }
      next()
    })
    this.$nuxt.$on('logout', () => {
      try {
        this.UA.stop()
      } catch (error) {}
    })
  },
  beforeDestroy () {
    console.log('Calling Destroyed')
    this.$nuxt.$off('dialNumber')
  },
  destroyed () {
    if (this.UA) {
      console.log('Stopping WebRTC')
      this.$store.commit('hasConnected', false)
      return this.UA.stop()
    }
  },
  methods: {
    ...mapMutations(['logout']),
    async changeSpeaker () {
      try {
        const devices = await navigator.mediaDevices.enumerateDevices()
        const audioDevices = devices.filter(device => device.kind === 'audiooutput')
        let nextIndex = this.audioOutDeviceIndex + 1
        let device = null
        if (!audioDevices[nextIndex]) {
          nextIndex = 0
          this.loud = true
        }

        if (audioDevices.length - 1 === nextIndex) { // Last Device
          device = audioDevices[nextIndex]
          this.loud = false
        } else {
          device = audioDevices[nextIndex]
        }

        this.audioOutDeviceIndex = nextIndex

        console.log({ device })

        console.log(`Audio was on ${this.$refs.call.sinkId}`)

        await this.$refs.call.setSinkId(device.deviceId)

        console.log(`Now audio is being played on ${this.$refs.call.sinkId}`)
      } catch (error) {
        console.log(error)
      }
    },
    async mute () {
      if (!this.ongoing) {
        return
      }
      this.muted = !this.muted
      if (this.muted) {
        await this.ongoing.mute()
      } else {
        await this.ongoing.unmute()
      }
    },
    call (phone) {
      if (!this.hasMicAccess) {
        return this.$nuxt.$emit('alert', { msg: 'Microphone Access denied.', role: 'warn', timeout: 3000 })
      }

      try {
        // const userCountry = parsePhoneNumber(this.creds.username).country -
        const phoneNumber = parsePhoneNumber(phone, 'ZW')
        if (phoneNumber.isValid()) {
          phone = phoneNumber.number
        }

        if (!phone || !phoneNumber.isValid()) {
          return this.$nuxt.$emit('alert', { msg: 'Enter a valid phone', role: 'warn', timeout: 3000 })
        }
        // Validate Phone
        this.phone = phoneNumber.formatInternational()
        this.status = 'Dialing'
        this.showDialing = true
        const dial = `sip:${phone}@${this.creds.host}`
        return this.UA.call(dial, {
          mediaConstraints: { audio: true, video: false }
        })
      } catch (error) {
        this.status = 'Invalid Phone'
        return this.close()
      }
    },
    connect () {
      console.log('Connecting...')
      if (this.UA) {
        return this.UA.start() // Reconnect
      }

      const { host, username, pwd: password } = this.creds
      const socket = new WebSocketInterface(`wss://${host}:8089/ws`)
      const configuration = {
        sockets: [socket],
        uri: `sip:${username}@${host}`,
        password
      }

      const ua = new UA(configuration)
      ua.on('connected', () => {
        console.log('WebRTC Connected')
        this.$store.commit('hasConnected', true)
      })
      ua.on('disconnected', ({ reason, code, error }) => {
        this.$store.commit('hasConnected', false)
        console.log('Disconnection Reason', { reason, code, error })
        // this.hangUp()
        // close page alert reason
      })

      ua.on('registrationFailed', ({ cause }) => {
        console.log('Failed', cause)
        if (cause === 'Authentication Error') {
          this.UA.stop()
          setTimeout(() => {
            this.logout()
            return this.$router.push('/auth')
          }, 3000)
          return this.$nuxt.$emit('alert', { msg: 'Auth Error. Re-login', role: 'error', timeout: 3000 })
        }
      })
      ua.on('newRTCSession', this.session)
      this.UA = ua
      return this.UA.start()
    },

    session ({ session }) {
      // const _this = this
      session.on('failed', ({ cause, message, originator }) => {
        console.log({ cause, message, originator })
        this.status = cause?.length < 15 ? cause : 'Failed'
        this.close()
      })

      session.on('progress', (p) => {
        this.status = 'Ringing'
        console.log(p, 'Progress')
      })

      session.on('confirmed', (c) => {
        console.log(c, 'Confirmed')
        this.status = '00:00'
        this.timer()
      })

      session.on('ended', (e) => {
        this.ongoing = null
        this.status = 'Ended'
        this.close()
      })
      // session.mute()

      // Puts audio in the DOM
      session.connection.ontrack = ({ streams }) => {
        for (let i = 0; i < streams.length; ++i) {
          const stream = streams[i]
          this.$refs.call.srcObject = stream
        }
      }

      // 'peerconnection': PeerConnectionListener;
      // 'connecting': ConnectingListener;
      // // 'sending': SendingListener;
      // 'progress': CallListener;
      // 'accepted': CallListener;
      // 'confirmed': ConfirmedListener;
      // 'ended': EndListener;
      // 'failed': EndListener;
      // 'newDTMF': DTMFListener;
      // 'newInfo': InfoListener;
      // 'hold': HoldListener;
      // 'unhold': HoldListener;
      // 'muted': MuteListener;
      // 'unmuted': MuteListener;
      // 'reinvite': ReInviteListener;
      // 'update': UpdateListener;
      // 'refer': ReferListener;
      // 'replaces': ReferListener;
      // 'sdp': SDPListener;

      return (this.ongoing = session)
    },
    close () {
      if (this.ongoingTimer) {
        clearInterval(this.ongoingTimer)
      }
      return setTimeout(() => {
        this.showDialing = false
        this.phone = ''
        this.status = ''
      }, 2500)
    },
    hangUp () {
      if (this.ongoing) {
        this.ongoing.terminate()
      }
    },
    timer () {
      let timer = 0
      let minutes
      let seconds
      this.ongoingTimer = setInterval(() => {
        minutes = parseInt(timer / 60, 10)
        seconds = parseInt(timer % 60, 10)

        minutes = minutes < 10 ? '0' + minutes : minutes
        seconds = seconds < 10 ? '0' + seconds : seconds

        this.status = `${minutes}:${seconds}`
        this.lastTimer = this.status
        ++timer
      }, 1000)
    }
  }
}
