import createPersistedState from 'vuex-persistedstate'
import SecureLS from 'secure-ls'
const ls = new SecureLS({ isCompression: false })

const persist = createPersistedState({
  storage: {
    getItem: key => ls.get(key),
    setItem: (key, value) => ls.set(key, value),
    removeItem: key => ls.remove(key)
  }
})

export const plugins = [persist]

export const state = () => ({
  deposits: [],
  cursor: null,
  balance: '0.00'
})

export const getters = {
}

export const mutations = {
  addDeposit: (state, deposit) => {
    // state.deposits = []
    let deposits = [...state.deposits]
    if (deposits.findIndex(d => d.hash === deposit.hash) !== -1) {
      return deposits
    }
    deposits.push(deposit)
    deposits = deposits.sort((a, b) => new Date(b.date) - new Date(a.date))
    state.deposits = deposits
  },
  flushDeposits: state => (state.deposits = []),
  setCursor: (state, cursor) => cursor ? (state.cursor = cursor) : (state.cursor = null),
  newBalance: (state, balance) => balance ? (state.balance = balance) : (state.balance = '0.00')
}

