
import { mapMutations, mapState } from 'vuex'

export default {
  data () {
    return {
      settings: false,
      isDark: true
    }
  },
  computed: {
    ...mapState(['darkTheme'])
  },
  watch: {
    settings (d) {
      this.$nuxt.$emit('toggle_menu', d, 'settings')
    },
    isDark (val) {
      // this.$nuxt.$emit('toggle_menu', val, 'fav_details')
      return this.setTheme(val)
    }
  },
  mounted () {
    this.$nuxt.$on('settings', (open) => {
      this.settings = open
    })
    this.isDark = this.darkTheme
  },
  methods: {
    ...mapMutations(['setTheme'])

  }
}
