
import { mapMutations } from 'vuex'

export default {
  data () {
    return {
      details: false,
      edit: false,
      contact: {
        tel: null,
        name: '',
        id: null
      },
      tel: '',
      name: ''
    }
  },
  watch: {
    details (d) {
      this.$nuxt.$emit('toggle_menu', d, 'fav_details')
    },
    edit (d) {
      this.$nuxt.$emit('toggle_menu', d, 'update_contact')
    }
  },
  mounted () {
    this.$nuxt.$on('fav_details', (contact) => {
      if (!contact) {
        return (this.details = false)
      }
      this.contact = contact
      console.log(contact)
      this.details = true
    })
    this.$nuxt.$on('update_contact', (contact) => {
      if (!contact) {
        return (this.edit = false)
      }
      this.tel = this.contact.tel
      this.name = this.contact.name
      this.edit = true
    })
  },
  methods: {
    ...mapMutations(['deleteContact', 'updateContact']),
    removeContact () {
      this.details = false
      this.deleteContact(this.contact)
      return this.$nuxt.$emit('alert', { msg: 'Contact was deleted', role: 'success', timeout: 3000 })
    },
    changeContact () {
      this.edit = false
      const newContact = {
        tel: this.tel,
        name: this.name,
        id: this.contact.id
      }
      console.log(this.contact.id)
      this.updateContact(newContact)
      return this.$nuxt.$emit('alert', { msg: 'Contact was updated', role: 'success', timeout: 3000 })
    },
    editContact () {
      this.details = false
      setTimeout(() => {
        this.$nuxt.$emit('update_contact', this.contact)
      }, 100)
    }
  }
}
