export default async function ({ store, $axios }) {
  const $nuxt = store._vm.$nuxt
  const { creds: { username: identifier, pwd: password } } = store.state
  if (store.getters.validToken) {
    return
  }
  $nuxt.$emit('page_loader', true, 3000)
  console.log('Loading...')
  try {
    const creds = { identifier, password }
    console.log(creds)
    const { data: { jwt } } = await $axios.post('auth/local', creds)
    if (jwt) {
      store.commit('setToken', jwt)
    }
    store.commit('serverAuthError', false)
  } catch (error) {
    store.commit('serverAuthError', true)
  }
  $nuxt.$emit('page_loader')
}
