
import { mapMutations, mapState } from 'vuex'

export default {
  setup () {
    const menuHider = document.getElementsByClassName('menu-hider')
    if (!menuHider.length) {
      const hider = document.createElement('div')
      hider.setAttribute('class', 'menu-hider')
      document.body.insertAdjacentElement('beforebegin', hider)
    }
    setTimeout(function () {
      if (menuHider[0].classList.contains('menu-active')) {
        menuHider[0].classList.remove('menu-active')
      }
    }, 0)
  },

  data () {
    return {
      loading: true,
      requestAccess: true,
      isActive: false,
      showNumPad: false,
      currentMenu: 'NO-MENU',
      closeEvent: false,
      dropMenu: false,
      showOnline: false
    }
  },
  head () {
    return {
      bodyAttrs: {
        class: this.darkTheme ? 'theme-dark' : 'theme-light'
      },
      htmlAttrs: {
        class: this.darkTheme ? 'tw-dark' : 'tw-light'
      }
    }
  },
  computed: {
    ...mapState(['hasMicAccess', 'darkTheme', 'creds', 'connected', 'token_exp', 'contacts']),
    onlineStatus () {
      return this.$nuxt?.isOnline
    },
    hidePhoneBookOn () {
      return this.$route.path !== '/' && '/profile/'.includes(this.$route.path)
    }
  },
  watch: {
    onlineStatus (_status) {
      this.showOnline = true
      setTimeout(() => (this.showOnline = false), 2000)
    }
  },
  beforeDestroy () {
    this.$nuxt.$off('page_loader')
    this.$nuxt.$off('reload')
  },
  mounted () {
    if (!this.hasMicAccess) {
      this.checkMicState()
    }
    this.$nuxt.$on('reload', this.reload)
    this.$nuxt.$on('logout', () => {
      this.loading = true
      this.logout()
      this.$store.commit('wallet/flushDeposits')
      this.$store.commit('wallet/setCursor')
      return this.$router.push('/auth')
    })

    this.$nuxt.$on('page_loader', (load, timeout = 1000) => {
      if (!load) {
        return setTimeout(function () {
          this.loading = false
        }, 100)
      }
      this.loading = true
      setTimeout(() => (this.loading = false), timeout)
    })

    this.$nuxt.$on('toggle_menu', (menu, ev) => {
      this.currentMenu = ev
      const menuHider = document.getElementsByClassName('menu-hider')
      if (!this.closeEvent) {
        menuHider[0].addEventListener('click', () => {
          console.log(`Closing ${this.currentMenu}`)
          return this.$nuxt.$emit(this.currentMenu)
        })
        this.closeEvent = true
      }
      if (menu) {
        return menuHider[0].classList.add('menu-active', ev)
      }
      return menuHider[0].classList.remove('menu-active', ev)
    })

    // Close dropdown menu
    document.body.addEventListener('mouseup', (e) => {
      if (this.dropMenu) {
        this.dropMenu = false
      }
    }, true)
    setTimeout(() => {
      this.loading = false
    }, 200)

    this.$nuxt.$on('contactsFound', (contacts) => {
      if (!contacts?.length) {
        return this.$nuxt.$emit('alert', { msg: 'No Contact Selected', role: 'warn', timeout: 3000 })
      }
      contacts.forEach(({ name, tel }) => {
        let phones = tel.map(c => c.split(' ').join('')) // Remove spaces
        if (phones.length > 1) {
          // Remove duplicates
          phones = phones.filter((v, i) => {
            return phones.indexOf(v) === i
          })
        }
        if (phones) {
          let contactNum = 0
          for (let i = 0; i < phones.length; i++) {
            const tel = phones[i]
            const found = this.contacts.find(c => c.tel === tel) // TODO! Format & validate contact here
            if (!found) {
              contactNum++
              const num = contactNum > 1 ? ` (${contactNum})` : ''
              this.addContact({ name: `${name}${num}`, tel })
            }
          }
        }
      })
    })
  },
  methods: {
    ...mapMutations(['setMicAccess', 'logout', 'addContact']),
    async checkMicState () {
      try {
        const { state } = await navigator.permissions.query({
          name: 'microphone'
        })
        console.log({ state })
        if (state === 'granted') {
          return this.setMicAccess(true)
        } else if (state === 'prompt') {
          return this.setMicAccess(false)
        } else {
          this.$nuxt.$emit('alert', { msg: 'Microphone Access Denied.', role: 'warn', timeout: 3000 })
          return this.setMicAccess(true)
        }
      } catch (error) {
        // alert device not supported
      }
    },
    allowAccess () {
      if (!navigator.getUserMedia) {
        navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia ||
                          navigator.mozGetUserMedia || navigator.msGetUserMedia
      }
      console.log(navigator.getUserMedia)
      if (navigator.getUserMedia) {
        navigator.getUserMedia({ audio: true, video: false }, console.log, (e) => {
          return this.$nuxt.$emit('alert', { msg: 'Access denied.', role: 'warn', timeout: 3000 })
        })
        return this.setMicAccess(true)
      } else {
        alert('App not supported in this browser.')
      }
    },

    async phoneBook () {
      const supported = ('contacts' in navigator && 'ContactsManager' in window && 'getProperties' in navigator.contacts)
      if (!supported) {
        return this.$nuxt.$emit('alert', { msg: 'Error! Use google chrome', role: 'warn', timeout: 3000 })
      }
      try {
        const availableProperties = await navigator.contacts.getProperties()

        if (availableProperties.includes('address')) {
          const contactProperties = ['name', 'tel']

          const contacts = await navigator
            .contacts
            .select(
              contactProperties,
              { multiple: true }
            )
          return this.$nuxt.$emit('contactsFound', contacts)
        } else {
          console.log("Contact Picker API on your device doesn't support address property")
        }
      } catch (e) {
        console.log('Unexpected error happened in Contact Picker API', e)
        return this.$nuxt.$emit('alert', { msg: 'Error! Try google chrome', role: 'warn', timeout: 3000 })
      }
    },
    reload () {
      this.loading = true
      return window.location.reload(true)
    }
  }
}
