
import { mapState } from 'vuex'
import { NotFoundError, Server } from 'stellar-sdk'
import Spinner from './Utils/Spinner.vue'
export default {
  components: { Spinner },
  props: {
    isNav: Boolean
  },
  data () {
    return {
      credits: null,
      reloadBalTimer: null,
      voucher: '',
      processing: false,
      invalid_voucher: false,
      server: new Server(this.$config.stellar),
      retried: false
    }
  },
  computed: {
    ...mapState(['creds', 'cdr', 'wallet', 'darkTheme']),
    fewDeposits () {
      return this.wallet.deposits.slice(0, 3)
    }
  },
  async mounted () {
    if (this.isNav) {
      await this.loadBalance()
      this.streamPayments()
    }
  },

  methods: {
    async submitVoucher () {
      const reg = /^\d{9,18}/
      if (!reg.test(this.voucher)) {
        return (this.invalid_voucher = true)
      }
      if (this.processing) {
        return
      }
      this.processing = true
      try {
        const { data: { data, message } } = await this.$axios.$post('v0.1/redeem', { voucher: this.voucher })
        console.log(data, { message })
        this.$nuxt.$emit('topAlert', { msg: message, role: 'success' })
        this.voucher = ''
      } catch (error) {
        const msg = error.response?.data?.error.message
        this.$nuxt.$emit('topAlert', { msg: msg || 'Error. Try again', role: 'error' })
        this.retried = false
      }
      this.processing = false
    },
    async loadBalance () {
      try {
        const account = await this.server.loadAccount(this.creds.account)
        const [bal] = account.balances.filter(b => b.asset_code === this.creds.code)
        const credits = Number(bal.balance).toFixed(2)
        this.credits = credits
        this.$store.commit('wallet/newBalance', credits)
      } catch (error) {
        if (error instanceof NotFoundError) {
          this.credits = '0.00'
        } else {
          console.error('Error getting balance', error)
        }
      }
    },
    streamPayments () {
      try {
        const stream = this.server
          .payments()
          .forAccount(this.creds.account)
        if (this.wallet.cursor) {
          stream.cursor(this.wallet.cursor)
        }
        const streaming = stream.stream({
          onerror: (e) => {
            console.log('NEW_ACCOUNT')
            const MIN = 1000 * 60
            streaming()
            if (!this.creds.username) {
              return
            }
            return setTimeout(() => {
              console.log('RETRYING...')
              return this.streamPayments()
            }, MIN)
          },
          onmessage: (payment) => {
            if (payment.type !== 'payment' && payment.asset_type) {
              return
            }
            return this.handlePayment(payment)
          }
        })
      } catch (error) {

      }
    },
    async handlePayment (payment) {
      // eslint-disable-next-line camelcase
      const { transaction, from, to, transaction_hash: hash, paging_token: cursor, asset_code: currency, amount, created_at: date } = payment
      const { memo } = await transaction()
      if (to === this.creds.account) {
        const [type, symbol = currency] = memo.split(':')
        const deposit = {
          date,
          amount: Number(amount).toFixed(2),
          type,
          hash,
          symbol
        }

        const isAdded = this.wallet.deposits.findIndex(d => d.hash === hash) !== -1
        if (isAdded) {
          return
        }
        this.isLast(deposit, 'DEP', cursor)
        return this.$store.commit('wallet/addDeposit', deposit)
      }

      if (from === this.creds.account) {
        const [symbol, id, seconds] = memo.split('I')
        if (!id) {
          const [type, receiverSymbol, receiver, currencySymbol = this.creds.symbol] = memo.split(':') // Share Credit
          if (!symbol) {
            return
          }
          const share = {
            date,
            amount: `${Number(amount).toFixed(2)}`,
            type,
            hash,
            receiver,
            receiverSymbol,
            symbol: `-${currencySymbol}`
          }

          const isAdded = this.wallet.deposits.findIndex(d => d.hash === hash) !== -1
          if (isAdded) {
            return
          }
          this.isLast(share, 'DEP', cursor)
          return this.$store.commit('wallet/addDeposit', share)
        }

        const cdr = {
          symbol,
          id,
          seconds,
          duration: (new Date(seconds * 1000)).toUTCString().match(/(\d\d:\d\d:\d\d)/)[0],
          callee: '',
          amount: Number(amount).toFixed(2),
          date
        }
        const isAdded = this.cdr.records.findIndex(c => c.id === cdr.id) !== -1
        if (isAdded) {
          return
        }
        this.isLast(cdr, 'CDR', cursor)
        return this.$store.commit('cdr/addCDR', cdr)
      }
    },
    dateToNum (date = null) {
      if (!date) {
        return new Date().getDate()
      }
      return new Date(date).getTime()
    },
    isLast (data, type, cursor) {
      if (this.reloadBalTimer) {
        clearTimeout(this.reloadBalTimer)
      }
      this.reloadBalTimer = setTimeout(() => {
        this.loadBalance()
        const MIN_AGO = 1000 * 60 * 5
        const secDiff = this.dateToNum() - this.dateToNum(data.date)
        this.$store.commit('wallet/setCursor', cursor)
        if (secDiff < MIN_AGO) {
          const msg = type === 'DEP' ? 'Transaction confirmed' : `Call lasted: ${data.duration}`
          this.$nuxt.$emit('alert', { msg, role: 'success', timeout: 3300 })
        }
      }, 1000 * 6)
    },
    relativeTime (date) {
      const relative = new Intl.RelativeTimeFormat(
        'en-GB', {
          numeric: 'auto'
        }
      )

      const time = new Intl.DateTimeFormat(
        'en-GB', {
          hour: 'numeric',
          minute: 'numeric'
        }
      )

      const long = new Intl.DateTimeFormat(
        'en-GB', {
          day: 'numeric',
          month: 'short',
          hour: 'numeric',
          minute: 'numeric'
        })

      const fDate = new Date(date)

      const now = new Date().setHours(0, 0, 0, 0)
      const then = new Date(date).setHours(0, 0, 0, 0)
      const days = (then - now) / 86400000
      if (days > -2) {
        return `${relative.format(days, 'day')}, ${time.format(fDate)}`
      }
      return long.format(fDate)
    }
  }
}
