export default function ({ $axios, store }) {
  $axios.onRequest(async (config) => {
    try {
      const { url = '' } = config
      if (url !== 'auth/local' && !url.includes('register/')) {
        const { token } = store.state.creds
        $axios.setToken(token, 'Bearer')
      }
      if (url === 'auth/local') {
        console.log('Remove token')
        // $axios.setToken('', 'Bearer')
        await $axios.setToken(false)
      }
    } catch (error) {
      console.log('Could not update token:', error)
    }
    return config
  })
}
