import createPersistedState from 'vuex-persistedstate'
import SecureLS from 'secure-ls'
const ls = new SecureLS({ isCompression: false })

const persist = createPersistedState({
  storage: {
    getItem: key => ls.get(key),
    setItem: (key, value) => ls.set(key, value),
    removeItem: key => ls.remove(key)
  }
})

export const plugins = [persist]

export const state = () => ({
  creds: {},
  hasMicAccess: false,
  darkTheme: false,
  contacts: [],
  connected: false,
  token_exp: 0,
  serverAuthError: false
})

export const getters = {
  getCreds: state => state.creds,
  darkTheme: state => state.darkTheme,
  getContacts: state => state.contacts,
  validToken: (state) => {
    const { token_exp: tokenExp, creds: { token } } = state
    return token && tokenExp > new Date().getTime()
  }
}

const UUIDv4 = new function () {
  function generateNumber (limit) {
    const value = limit * Math.random()
    return value | 0
  }
  function generateX () {
    const value = generateNumber(16)
    return value.toString(16)
  }
  function generateXes (count) {
    let result = ''
    for (let i = 0; i < count; ++i) {
      result += generateX()
    }
    return result
  }
  function generateVariant () {
    const value = generateNumber(16)
    const variant = (value & 0x3) | 0x8
    return variant.toString(16)
  }
  this.generate = function () {
    const result = generateXes(8) +
      '-' + generateXes(4) +
      '-' + '4' + generateXes(3) +
      '-' + generateVariant() +
      generateXes(3) + '-' + generateXes(12)
    return result
  }
}()

export const mutations = {
  saveCreds: (state, creds) => creds && creds.username ? (state.creds = creds) : (state.creds = {}),
  setMicAccess: (state, access) => access ? (state.hasMicAccess = access) : (state.hasMicAccess = false),
  logout: (state) => {
    state.creds = {}
    state.token_exp = 0
  },
  setTheme: (state, bool) => (state.darkTheme = bool),
  hasConnected: (state, bool) => (state.connected = bool),
  addContact: (state, contact) => {
    contact.id = UUIDv4.generate()
    return state.contacts.push(contact)
  },
  deleteContact: (state, contact) => {
    state.contacts = state.contacts.filter((c) => {
      return c.tel && contact.id !== c.id
    })
    return true
  },
  updateContact: (state, updatedContact) => {
    const index = state.contacts.findIndex(contact => contact.id === updatedContact.id)
    if (index === -1) {
      return false
    }
    return state.contacts.splice(index, 1, updatedContact)
  },
  setToken: (state, token) => {
    if (!token) {
      state.token_exp = 0
      return (state.creds.token = null)
    }
    const d = new Date()
    d.setDate(d.getDate() + 5)
    state.token_exp = d.getTime()
    state.creds.token = token
    console.log(d)
  },
  serverAuthError: (state, bool) => (state.serverAuthError = bool)
}

