
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      loaded: false
    }
  },
  head () {
    return {
      bodyAttrs: {
        class: this.darkTheme ? 'theme-dark' : 'theme-light'
      }
    }
  },
  computed: mapState(['darkTheme']),
  mounted () {
    setTimeout(() => (this.loaded = true), 210)
    try {
      if (window.matchMedia('(prefers-color-scheme: dark)').matches) {
        console.log('Setting dark', this.darkTheme)
        if (!this.darkTheme) {
          this.setTheme(true)
        }
      } else if (window.matchMedia('(prefers-color-scheme: light)').matches) {
        console.log('light')
      } // it is always light
    } catch (error) {
    }
  },
  methods: {
    ...mapMutations(['setTheme'])
  }
}
