import createPersistedState from 'vuex-persistedstate'
import SecureLS from 'secure-ls'
const ls = new SecureLS({ isCompression: false })

const persist = createPersistedState({
  storage: {
    getItem: key => ls.get(key),
    setItem: (key, value) => ls.set(key, value),
    removeItem: key => ls.remove(key)
  }
})

export const plugins = [persist]

export const state = () => ({
  records: []
})

export const getters = {
  fetchNumbers: state => state.records.findIndex(r => !r.callee) !== -1
}

export const mutations = {
  addCDR: (state, cdr) => {
    let records = [...state.records]
    if (records.findIndex(call => call.id === cdr.id) !== -1) {
      return
    }
    records.push(cdr)
    records = records.sort((a, b) => new Date(b.date) - new Date(a.date))
    state.records = records
  },
  updateCDR: (state, cdr) => {
    const cdrI = state.records.findIndex(call => call.id === cdr.id)
    if (cdrI === -1) {
      return
    }
    const record = { ...state.records[cdrI], callee: cdr.callee }
    return state.records.splice(cdrI, 1, record)
  }
}

// async
export const actions = {
  async getCDRNumbers ({ commit, state, dispatch, rootState }) {
    const cdrIDs = state.records.filter(r => !r.callee).map(r => r.id)
    if (!cdrIDs.length) {
      return
    }
    const data = {
      ids: cdrIDs.join(',')
    }
    try {
      const { data: { ids, idsData } } = await this.$axios.post('v0.1/callee', data)
      if (!ids) {
        return
      }
      idsData.forEach(cdr => commit('updateCDR', cdr))
    } catch (error) {
      if (error.response?.statusText === 'Unauthorized') {
        const tokenExp = rootState.token_exp
        const token = rootState.creds?.token
        if (token && tokenExp > new Date().getTime()) {
          await commit('setToken', null, { root: true })
        }
        return setTimeout(() => {
          return dispatch('getCDRNumbers')
        }, 6000)
      }
      console.error(error)
    }
  }
}
