
export default {
  data () {
    return {
      showNumPad: false,
      num: '',
      hideCustomVK: false,
      holdInterval: null,
      ms: 0,
      cursor: 0
    }
  },
  watch: {
    $route (newVal) {
      this.showNumPad = newVal.query?.showNumPad || false
    }
  },
  mounted () {
    this.cursor = this.num.length || 0
    this.$router.beforeEach((_to, _from, next) => {
      if (this.showNumPad) {
        this.showNumPad = false
        next(false)
        return ''
      }
      next()
    })
  },
  methods: {
    plusStart () {
      this.keyNum('0')
      if (!this.holdInterval) {
        this.holdInterval = setInterval(() => {
          this.ms++
          this.delNum()
          this.vibrate(10)
          this.keyNum('+')
        }, 350)
      }
    },
    vibrate (ms = 5) {
      try {
        window.navigator.vibrate(ms)
      } catch (error) {}
    },
    plusStop () {
      clearInterval(this.holdInterval)
      this.holdInterval = false
    },
    keyNum (num) {
      this.vibrate()
      if (!this.num) {
        this.num = num
      } else if (this.cursor === this.num.length) {
        this.num = `${this.num}${num}`
      } else {
        this.num = [this.num.slice(0, this.cursor), num, this.num.slice(this.cursor)].join('')
      }
      this.cursor = this.cursor + 1
    },
    delNum () {
      if (!this.num) {
        return
      }
      this.vibrate()
      this.num = this.num.substr(0, this.cursor - 1) + this.num.substring(this.cursor)
      if (this.cursor > 0) {
        this.cursor = this.cursor - 1
      }
      if (!this.holdInterval) {
        this.holdInterval = setInterval(() => {
          this.vibrate(10)
          this.ms++
          this.cursor = 0
          this.num = ''
        }, 400)
      }
    },
    setCursor (e) {
      try {
        navigator.virtualKeyboard.hide()
      } catch (error) {
        this.hideCustomVK = true
      }
      if (!this.num?.length) {
        return
      }
      this.cursor = e.target.selectionStart
    },
    makeCall () {
      this.showNumPad = false
      setTimeout(() => {
        this.$nuxt.$emit('dialNumber', this.num)
        setTimeout(() => {
          this.num = ''
        }, 100)
      }, 250)
    }
  }
}
