
import { mapState } from 'vuex'

export default {
  props: {
    message: {
      default: 'Failed to load recent call, refresh page',
      type: String
    }
  },
  data () {
    return {
      spin: false
    }
  },
  computed: mapState(['serverAuthError']),
  methods: {
    reload () {
      this.spin = true
      setTimeout(() => this.$nuxt.$emit('reload'), 250)
    }
  }
}
