
import { mapState } from 'vuex'

export default {
  setup () {
    setTimeout(function () {
      const preloader = document.getElementById('preloader')
      if (preloader) { preloader.classList.add('preloader-hide') }
    }, 500)
    const menuHider = document.getElementsByClassName('menu-hider')
    if (!menuHider.length) {
      const hider = document.createElement('div')
      hider.setAttribute('class', 'menu-hider')
      document.body.insertAdjacentElement('beforebegin', hider)
    }
    setTimeout(function () {
      if (menuHider[0].classList.contains('menu-active')) {
        menuHider[0].classList.remove('menu-active')
      }
    }, 0)
  },
  data () {
    return {
      loaded: false,
      dropMenu: false,
      showOnline: false,
      title: 'Rates'
    }
  },
  head () {
    return {
      bodyAttrs: {
        class: this.darkTheme ? 'theme-dark' : 'theme-light'
      },
      htmlAttrs: {
        class: this.darkTheme ? 'tw-dark' : 'tw-light'
      }
    }
  },
  computed: {
    ...mapState(['darkTheme']),
    onlineStatus () {
      return this.$nuxt?.isOnline
    }

  },
  watch: {
    onlineStatus (_status) {
      this.showOnline = true
      setTimeout(() => (this.showOnline = false), 2000)
    }
  },
  beforeMount () {
    this.$nuxt.$on('full_title', title => (this.title = title))
  },
  mounted () {
    this.$nuxt.$on('toggle_menu', (menu, ev) => {
      this.currentMenu = ev
      const menuHider = document.getElementsByClassName('menu-hider')
      if (!this.closeEvent) {
        menuHider[0].addEventListener('click', () => {
          console.log(`Closing ${this.currentMenu}`)
          return this.$nuxt.$emit(this.currentMenu)
        })
        this.closeEvent = true
      }
      if (menu) {
        return menuHider[0].classList.add('menu-active', ev)
      }
      return menuHider[0].classList.remove('menu-active', ev)
    })
    // Close dropdown menu
    document.body.addEventListener('mouseup', (e) => {
      if (this.dropMenu) {
        this.dropMenu = false
      }
    }, true)
  },
  methods: {
    reload () {
      const preloader = document.getElementById('preloader')
      if (preloader) { preloader.classList.remove('preloader-hide') }
      return window.location.reload(true)
    }
  }
}
